import React, { useState, useEffect } from 'react';
import Hero from './Hero/Hero';
import About from './About/About';
import Projects from './Projects/Projects';
import Contact from './Contact/Contact';
import Footer from './Footer/Footer';
import SkillBar from 'react-skillbars';
import Title from './Title/Title';

import { PortfolioProvider } from '../context/context';

import { heroData, aboutData, skillsData, toolsData, colors, projectsData, contactData, footerData } from '../mock/data';


function App() {
  const [hero, setHero] = useState({});
  const [about, setAbout] = useState({});
  const [projects, setProjects] = useState([]);
  const [contact, setContact] = useState({});
  const [footer, setFooter] = useState({});

  useEffect(() => {
    setHero({ ...heroData });
    setAbout({ ...aboutData });
    setProjects([...projectsData]);
    setContact({ ...contactData });
    setFooter({ ...footerData });
  }, []);

  return (
    <PortfolioProvider value={{ hero, about, projects, contact, footer }}>
      <Hero />
      <About />
      <section id="projects">
      <div className="project-wrapper">
      <Title title="Skills" />
      <SkillBar skills={skillsData} colors = {colors}/>
      </div>
      </section>
      <section id="projects">
      <div className="project-wrapper">
      <Title title="Tools and Technologies" />
      <SkillBar skills={toolsData} colors = {colors}/>
      </div>
      </section>

      
      
      
      <Projects />
      <Contact />
      <Footer />
    </PortfolioProvider>
  );
}

export default App;
